
.select-end {
     width: 100%;
     display: -webkit-box;
     display: -ms-flexbox;
     display: flex;
     height: 100px;
     background: #3ff;
     -webkit-box-pack: center;
         -ms-flex-pack: center;
             justify-content: center;
     -webkit-box-align: center;
         -ms-flex-align: center;
             align-items: center;
     min-width: 700px;
}
.select-end > div {
   height: 30px;
   width: 80px;
   line-height: 30px;
   background: #fce;
   margin-right: 30px;
   text-align: center;
   cursor: pointer;
   border-radius: 10px;
}
.view {
  min-width: 700px;
}

.list-wrapper {
    width: 300px;
    border: 1px solid lime;
}
.list-header, .list-title {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      height: 30px;
      line-height: 30px;
}
.left-header , .left-title{
     -webkit-box-flex: 1;
         -ms-flex: 1;
             flex: 1;
}
.left-dir, .right-dir {
    color: rgb(56, 139, 255)
}
.normal-dir {
     color: rgb(204,204,204);
}



.front-wrapper {
   width: 100%;
   margin-top: 20px;
   min-width: 700px;
}
.change-msg {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 30px;
}
.page , .length {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    height: 30px;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.change-page, .change-length {
    margin-left: 10px;
    height: 20px;
    width: 50px;
    background: red;
    line-height: 20px;
    color: white;
    text-align: center;
    cursor: pointer;
}
.add-list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin-bottom: 30px;
}
.add-title, .add-num {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.add-btn {
    height: 20px;
    width: 50px;
    line-height: 20px;
    text-align: center;
    background: red;
    color: white;
    cursor: pointer;
}


